.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

main {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

header {
  display: flex;
  flex-direction: row;
  height: 10vmin;
  width: 100%;
  position: fixed;
  top: 0;
  padding: 1vmin;
  justify-content: space-between;
  vertical-align: center;
  font-size: calc(10px + 1vmin);
  background-color: darkslategray;
}

footer {
  display: flex;
  flex-direction: row;
  height: 2vmin;
  width: 100%;
  position: fixed;
  bottom: 0;
  padding: 1vmin;
  align-items: center;
  justify-content: center;
  vertical-align: center;
  font-size: calc(2px + 1vmin);
  background-color: darkslategray;
}

.body {
  display: flex;
  flex-direction: row;
  margin-top: 10vmin;
  margin-bottom: 2vmin;
  padding: 1vmin;
  font-size: calc(10px + 1vmin);
}

.logo {
  height: 10vmin;
  pointer-events: none;
  align-self: left;
}

.banner {
  display: flex;
  flex-direction: row;
}

.user {
  display: flex;
  flex-direction: column;
  padding: 1vmin 2vmin;
}

.wrapper-column {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.wrapper-row-top {
  display: flex;
  flex-direction: row;
  height: calc(100% - 24vmin);
  width: 100%;
  position: fixed;
  top: 12vmin;
}

.wrapper-row-bottom {
  display: flex;
  flex-direction: row;
  height: 8vmin;
  width: 100%;
  position: fixed;
  bottom: 4vmin;
}

.reference-wrapper {
  display: flex;
  flex-direction: column;
  width: 30%;
  overflow-y: auto;
  padding: 1vmin;
}

.chat-wrapper {
  display: flex;
  flex-direction: column;
  width: 70%;
}

.confirm-wrapper {
  display: flex;
  flex-direction: row;
  width: 80%;
  padding: 1vmin;
}

.submit-wrapper {
  display: flex;
  flex-direction: row;
  width: 20%;
  padding: 1vmin;
}

.input-wrapper {
  display: flex;
  flex-direction: row;
  position: fixed;
  padding: 1vmin;
  width: calc(70% - 4vmin);
  bottom: 12vmin;
}

.new {
  width: 10%;
}

.input {
  width: 80%;
}

.send {
  width: 10%;
}

.messages {
  margin-bottom: 8vmin;
  padding: 1vmin;
  overflow-y: auto;
}

.message-row {
  display: flex;
  align-items: center;
  margin-bottom: 1vmin;
}

.assistant-row {
  justify-content: flex-start;
}

.user-row {
  justify-content: flex-end;
}

.message {
  padding: 1vmin;
  border-radius: 2vmin;
  max-width: 80%;
  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: pre-wrap;
}

.user-msg {
  background-color: #0080ff;
  color: white;
  align-self: flex-end;
  margin-left: 10vmin;
}

.assistant-msg {
  background-color: #09ff00;
  color: black;
  align-self: flex-start;
  margin-right: 10vmin;
}

.profile-picture-wrapper {
  display: flex;
  align-items: center;
}

.profile-picture-wrapper.left {
  margin-right: 1vmin;
}

.profile-picture-wrapper.right {
  margin-left: 1vmin;
}

.profile-picture {
  width: 4vmin;
  height: 4vmin;
  border-radius: 50%;
}

.chat-limit-message {
  color: red;
  width: 100%;
  text-align: center;
  padding: 1vmin;
  font-weight: bold;
}


@media (max-width: 768px) {
  .wrapper-row-top {
    flex-direction: column;
    position: fixed;
    height: calc(100% - 32vmin);
    width: 100%;
    top: 12vmin;
  }

  .reference-wrapper {
    flex-direction: column;
    height: 40%;
    width: 100%;
    overflow-y: auto;
  }

  .chat-wrapper {
    flex-direction: column;
    height: 60%;
    width: 100%;
    overflow-y: auto;
  }

  .wrapper-row-bottom {
    flex-direction: column; 
    position: fixed;
    height: 16vmin;
    width: 100%;
    bottom: 4vmin;
  }

  .confirm-wrapper, .submit-wrapper  {
    flex-direction: row;
    height: 8vmin;
    width: 100%;
  }

  .input-wrapper {
    width: calc(100% - 4vmin);
    bottom: 20vmin;
  }

  .new {
    width: 20%;
  }

  .input {
    width: 60%;
  }
  
  .send {
    width: 20%;
  }

  .messages {
    margin-bottom: 16vmin;
  }
}
